import {mapState,mapGetters,mapActions,mapMutations} from "vuex"

export default{
	components:{
		
	},
	props:{

	},
	data() {
		return {
			name:'',
			sortValue:'',
			isTouch:true
		}
	},
	computed:{
		...mapState({
			goodsTypeList:state=>state.goods.goodsTypeList
		}),
		...mapGetters([

		]),
		
	},
	methods:{
		...mapMutations([
			'updateGoodsTypeList'
		]),
		...mapActions([
		]),
		
		showPopup(){
			this.$refs.popup.open()
		},
		change(e){
			if(!e.show){
				this.name = ''
				this.sortValue = ''
				this.item = null
			}
		},
		nameInput(e){
			this.name = e.detail.value
		},
		sortValueInput(e){
			this.sortValue = e.detail.value
		},
		changeGoodsType(string){
			var _that = this;
			var title = '新增中...'
			var urlStr = '/api/ProductType/add'
			var list = {
				Name:_that.name,
				SortValue:_that.sortValue
			}
			if(string === 'change'){
				title = '修改中...'
				urlStr = '/api/ProductType/update '
				list = {
					Name:_that.name,
					SortValue:_that.sortValue,
					ID:_that.item.ID
				}
			}
			uni.showLoading({title:title})
			_that.$H.post(urlStr,list,{
				token:true
			}).then(res=>{
				console.log(res)
				uni.hideLoading()
				if(res.status === 0){
					let tempList = _that.goodsTypeList
					if(string === 'change'){
						for (var i = 0; i < tempList.length; i++) {
							if(tempList[i].ID === res.data.ID){
								tempList[i].Name = res.data.Name
								tempList[i].SortValue = res.data.SortValue
								return
							}
						}
					}else{
						this.goodsTypeList.push(res.data)
					}
				}else{
					uni.showToast({
						title:res.message, 
						icon: 'none',
						duration:1000
					})
				}
			})
		}
	}
}
